.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.me-auto {
  font-weight: bold;
}

.col-md-3 {
  margin-top: 30px;
}

.col-md-2 {
  margin-top: 30px;
}

.Title_button {
  border-radius:20px;
  width:90px;
  margin: 10px 5px;
  border: 2px solid lightgray;
  font-weight: bold;
}

.Title_button:hover {
  border: 2px solid darkgray;
}

.col-md-4 {
  margin-top: 4%;
}

.tennis, .tennis2 {
  position: relative;
}

.tennis > h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;

}

.tennis2 > h4 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;

}

.tennis > button {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  border: 1px solid white;
  padding: 10px 40px;
  background-color: rgba(0,0,0,0);
}

.tennis > button:hover, .tennis2 > button:hover  {
  border: 1px solid tomato;
  color: tomato;
  cursor: pointer;
}

.tennis2 > button {
  position: absolute;
  bottom: 10%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  border: 1px solid white;
  padding: 10px 40px;
  background-color: rgba(0,0,0,0);

}

.banner_textbox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.banner_title {
  font-weight: bold;
}

.banner_t1 {
  padding: 10px 0;
  font-size: 35px;
}

.banner_button {
  border: 1px solid white;
  padding: 10px 20px;
  background-color: rgba(0,0,0,0);
  color: white;

}

.banner_button:hover {
  border: 1px solid black;
  color: black;
}

.more {
  margin-top: 20px;
  padding: 10px 30px;
  border-radius: 0px;
  font-weight: bold;
}

.product_title {
  text-align: left;
  margin-top: 15px;
}

.product_price {
  text-align: left;
  margin-top: 15px;
  font-weight: bold;
}

.card_title {
  color: white;
  font-weight: bold;
  font-size: 33px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.sub_product {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.sub_product > div > img {
  border-radius: 50%;
  width: 80%;
}

.sub_product > div > h6 {
  font-weight: bold;
  margin: 15px 0 10px 0;
}

.col-lg-7 {
  margin-top: 50px;
}  

.col-lg-5 {
  margin-top: 50px;
}  

.col-lg-5 > h4 {
  text-align: left;
  font-weight: bold;
  color: rgb(224, 0, 42);;
}

.col-lg-5 > h5  {
  text-align: left;
  font-weight: bold;
}


.info {
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  margin-top: 20px;

}

.info > tr > td {
  padding: 10px;
  text-align: left;
}

.info > tr > td > input {
  width: 10%;
  text-align: center;
  border: none;
}

.info > tr > td > button {
  border: none;
  width: 7%;
}


.info > tr > th {
  padding: 10px;
  text-align: left;
  width: 30%;
}


.clickbtn {
  padding: 10px 70px;
  margin: 20px;
  font-weight: bold;
}

.detail_tab {
  margin-top: 70px;
}

.start {
  transform: scale(0);

}
.end {
   transform: scale(1);
   transition :all 0.5s;
}

.refund {
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}

.refund > tr > th {
  padding: 10px;
  text-align: left;
  width: 30%;

}

.refund > tr > td {
  padding: 10px;
  text-align: left;

}

.review {
  border-top: 1px solid lightgray;
  padding: 20px;
  text-align: left;

}

.sortbtn {
  padding: 10px 50px;
  margin: 20px;
  font-weight: bold;
}